export async function getUserProfile(userId: string): Promise<{ user: User; joinedEvents: Event[] } | null> {
  const user = await getUserById(userId);
  if (!user) {
    console.warn(`用户个人资料未找到: ${userId}`);
    return null;
  }

  const allEvents = await getEvents();
  // Fetch all users who are participants in any event to enrich participant details later
  const participantUserIds = new Set<string>();
  allEvents.forEach(event => {
    event.participants.forEach(p => participantUserIds.add(p.id));
  });
  
  // Add current user ID to ensure their details are fetched if not in any event participant list.
  participantUserIds.add(user.id); 

  const allParticipantUsers: User[] = [];
  for (const pId of Array.from(participantUserIds)) {
    const pUser = await getUserById(pId);
    if (pUser) {
      allParticipantUsers.push(pUser);
    }
  }
  
  const joinedEventIds = user.joinedEventIds || [];
  const joinedEventsRaw = allEvents.filter(event => joinedEventIds.includes(event.id));

  const enrichedJoinedEvents = joinedEventsRaw.map(ev => {
    const participantsWithDetails = ev.participants.map(p => {
      const participantUser = allParticipantUsers.find(u => u.id === p.id);
      return {
        ...p,
        id: p.id,
        username: participantUser?.username || p.username || '未知用户',
        avatarUrl: participantUser?.avatarUrl || p.avatarUrl,
        op: participantUser?.op ?? p.op ?? 0,
      };
    });
    return { ...ev, participants: participantsWithDetails };
  });

  return {
    user: {...user}, // Return a copy
    joinedEvents: enrichedJoinedEvents.map(e => ({...e})) // Return a copy
  };
}

    